import { Injectable } from '@angular/core';
import { BehaviorSubject,Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Restaurant } from 'src/Table/table';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';
import { AppError } from '../common/app-error';
@Injectable({
  providedIn: 'root'
})
export class RestaurantService {
 
  private restaurantCollectionList: AngularFirestoreCollection<any>;
  private restaurantList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.restaurantCollectionList = db.collection<Restaurant>('Restaurant');
  }
  create(restaurantObj) { 
    return this.restaurantCollectionList.add(restaurantObj)
  }
  getAllRestaurant() {
    this.restaurantList = this.restaurantCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.restaurantList;
  }
  getRestaurant(id) {
    return this.restaurantCollectionList.doc<any>(id).valueChanges();
  }
  updateRestaurant(restaurantObj: Restaurant, id: string) {
    return this.restaurantCollectionList.doc(id).update(restaurantObj)
    .catch(this.handleError);
  }
  removeRestaurant(id) {
    return this.restaurantCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
